body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  background-color: rgb(255, 248, 239);
  min-height: 100vh;
}
.NavBar {
    display: flex;
    align-items: center;
    margin: 0;
    padding: 0;
    top: 0;
    left: 0;
    height: 56px;
    width: 100%;
    background-color: rgb(228, 221, 213);
}

.NavBar img {
    margin: 5px 30px;
    height: 80%;
    width: auto;
}

.NavBar h3 {
    margin: 0;
    padding: 0;
    font-weight: 400;
    font-size: 26px;
}

.NavBar ul {
    display: flex;
    margin-left: auto;
    margin-right: 4%;
    justify-content: right;
    align-items: center;
    width: auto;
    padding: 0;
}

.NavBar ul li {
    display: inline-block;
    width: auto;
    list-style: none;
    border-right: 1px solid #aaa;
    padding: 0px 10px;
    font-size: 18px;
}

.NavBar ul li::before {
    content: "\200B";
}

.NavBar ul li a:link,
.NavBar ul li a:visited {
    text-decoration: none;
    color: #000;
}

.NavBar ul li a:hover {
    border-bottom: 1px solid #777;
    color: #000;
    transition: 0.2s;
}

.NavBar ul li a:active {
    border-bottom: 1px solid orange;
    transition: 0.2s;
}

@media (max-width: 670px) {
    .NavBar ul {
        display: none;
    }

    .navBar img:hover {
        cursor: pointer;
    }
}
.MainPage {
    width: 70%;
    margin: 2% auto;
    border: 1px solid #999;
    border-radius: 30px;
    background-color: white;
    font-family: "Noto Serif TC", serif;
}

.MainPage h3 {
    padding: 0 2%;
    font-size: 1.5rem;
    text-align: center;
}

.MainPage p {
    text-align: left;
    font-size: 1.2rem;
    margin: 0;
    padding: 8px 16px;
}

.MainPage p:last-child {
    padding-bottom: 16px;
}

.MainPage ul {
    margin: 1% 0;
}

.MainPage ul li {
    list-style: square;
}

.MainPage hr {
    width: 70%;
}
.footer {
    background-color: #222;
    margin: 0;
    padding: 1% 2%;
}

.footer p {
    font-size: 0.8rem;
    color: white;
    font-family: "Noto Serif TC", serif;
    text-align: center;
}
.calculator {
    width: 70%;
    box-sizing: border-box;
    margin: 2% auto;
    padding: 1% 2%;
    border: 1px solid #999;
    border-radius: 30px;
    background-color: white;
    font-family: "Noto Serif TC", serif;
}

.calculator h3 {
    margin: 1% 0;
    padding: 0;
    font-size: 1.3rem;
    font-weight: 400;
    text-align: center;
}

.calculator form {
    text-align: left;
}

.calculator p span {
    font-size: 1rem;
    font-weight: 300;
    border-bottom: 1px solid #333;
    border-width: auto;
}

.inputContainer {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: center;
}

.inputRow {
    width: 90%;
    font-size: 1rem;
}

.inputRow input {
    display: inline-block;
    width: auto;
    font-size: 1rem;
}

.inputRow button {
    height: 100%;
    background-color: white;
    border: 1px solid #999;
    font-size: 1rem;
    border-radius: 5px;
    outline: 0;
    cursor: pointer;
    margin: 0% 2%;
}

.inputRow button:disabled {
    display: none;
}

.submit {
    text-align: center;
}

.submit button {
    width: 20%;
    margin: 1% auto;
    margin-top: 2%;
    padding: 1.5% 0;
    outline: 0;
    font-size: 1rem;
    border: 1px solid #888;
    border-radius: 15px;
    background-color: rgb(255, 248, 239);
}

.submit button:hover,
.submit button:active {
    background-color: rgb(248, 196, 129);
    transition: 0.2s ease-out;
    font-weight: 400;
    cursor: pointer;
}

.submit button:active {
    outline: 0;
    box-shadow: 2px 2px 5px #999;
    transition: 0.2s;
}

.alert {
    margin: 3% auto;
    padding: 3%;
    width: 80%;
    background-color: #fabbbb;
}
.alert p {
    margin: 0;
    padding: 0;
    text-align: center;
}

@media (max-width: 700px) {
    .calculator {
        width: 90%;
    }
    
    .inputContainer {
        flex-direction: column;
        justify-items: flex-start;
        padding: 1%;
    }

    .inputFields {
        padding: 2% 0;
        width: 90%;
    }
}
.inputFields {
    width: 50%;
    box-sizing: border-box;
    padding: 2% 2%;
    font-size: 1rem;
    font-family: "Noto Serif TC", serif;
}

.inputFields input {
    display: inline-block;
    width: 50%;
    font-size: 1rem;
    padding: 2% 3%;
    border: 1px solid black;
    border-radius: 5px;
}

@media (max-width: 700px) {
    .inputFields {
        padding: 2% 0;
        width: 90%;
    }
}
.electiveInput {
    width: 90%;
    padding: 2% 2%;
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1rem;
    font-family: "Noto Serif TC", serif;
}

.dropdown {
    padding: 0.75rem;
    height: 100%;
    font-size: 1rem;
    font-family: "Noto Serif TC", serif;
    background-color: white;
}

.electiveFields {
    width: 50%;
    box-sizing: border-box;
    padding: 0 2%;
    font-size: 1rem;
    font-family: "Noto Serif TC", serif;
}

.electiveFields input {
    display: inline-block;
    height: auto;
    width: 50%;
    font-size: 1rem;
    padding: 2% 3%;
    border: 1px solid black;
    border-radius: 5px;
}

@media (max-width: 700px) {
    .electiveInput {
        flex-direction: column;
    }

    .electiveFields {
        width: 100%;
        padding-top: 2%;
    }
}
.sideDrawer {
    position: fixed;
    width: 300px;
    max-width: 70%;
    height: 100%;
    top: 0;
    left: 0;
    box-sizing: border-box;
    background-color: rgb(228, 221, 213);
    z-index: 200;
    padding: 16px;
    font-size: 1.3rem;
    transition: 0.3s
}

.openDrawer {
    -webkit-transform: translateX(0);
            transform: translateX(0);
}

.closeDrawer {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
}

.drawerlogo {
    display: block;
    margin: 0 auto;
    height: 40px;
    width: auto;
}

.sideDrawer h3 {
    margin: 10px auto;
    font-weight: 300;
    border-bottom: 1px dotted #888;
    text-align: center;
}

.closeButton {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    top: 0;
    left: 0;
    height: 40px;
    width: 40px;
    margin: 10px;
    font-size: 20px;
    border-radius: 50%;
    cursor: pointer;
}

.sideDrawer ul {
    display: flex;
    flex-direction: column;
    padding: 0;
    margin: 4% 5%;
    align-items: left;
    width: auto;
    list-style: none;
}

.sideDrawer ul li {
    margin: 2% 0;
}

.sideDrawer ul li a:link,
.sideDrawer ul li a:visited {
    text-decoration: none;
    color: #000;
}

.sideDrawer ul li a:hover {
    border-bottom: 1px solid #777;
    color: #000;
    transition: 0.2s;
}

.sideDrawer ul li a:active {
    border-bottom: 1px solid orange;
    transition: 0.2s;
}

@media (min-width: 670px) {
    .sideDrawer {
        display: none;
    }
}
.resultPage {
    width: 90%;
    margin: 2% auto;
    padding-bottom: 2%;
    border: 1px solid #999;
    border-radius: 30px;
    background-color: white;
    font-family: "Noto Serif TC", serif;
}

.resultPage h3 { 
    text-align: center; 
}

.uniSelect, .categorySelect {
    display: flex;
    margin: 1rem auto;
    justify-content: space-evenly;
    flex-wrap: wrap;
    width: 90%;
    box-sizing: border-box;
}

.uniSelect button {
    box-sizing: border-box;
    display: inline-block;
    flex-shrink: 0;

    border: 1px solid darkgray;
    border-radius: 10px;
    width: 20%;
    margin: 0.5rem;
    padding: 0.5rem;
    transition: 0.3s ease-out;
    cursor: pointer;
    background-color: white;
}

.uniSelect button h3 {
    font-weight: 400;
    font-size: 1rem;
    font-family: "Noto Serif TC", serif;
    text-align: center;
    margin: 0;
}

.uniSelect img {
    display: block;
    max-height: 6rem;
    max-width: 100%;
    margin: 0.5rem auto;
}

.uniSelect button:hover,
.uniSelect button:active,
.uniSelect button.selected {
    box-shadow: 2px 2px rgba(180, 180, 180, 0.5);
    background-color: rgba(253, 212, 100, 0.5);
}

.categorySelect button {
    margin-bottom: 0.5rem;
    padding: 0.5rem;
    display: inline-block;
    flex-shrink: 0;
    width: 25%;
    transition: 0.3s ease-out;
    cursor: pointer;
    background-color: white;
    border: 0;
    border-bottom: 1px dotted gray;

    font-weight: 400;
    font-size: 1rem;
    font-family: "Noto Serif TC", serif;
    text-align: center;
}

.categorySelect button:hover {
    border-bottom: 1px solid darkgray;
}

.categorySelect .selected,
.categorySelect .selected:hover {
    border: 1px solid darkgray;
    border-radius: 1rem;

    box-shadow: 2px 2px rgba(180, 180, 180, 0.5);
    background-color: rgba(253, 212, 100, 0.5);
    transition: none;
}

@media (max-width: 700px) {
    .uniSelect button {
        width: 40%;
    }

    .calMethod {
        display: none;
    }
}
.modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
    background-color: rgba(0, 0, 0, 0.5);
    justify-content: center;
    align-items: center;
    transition: 0.2s;
}

.openModal {
    -webkit-transform: translateX(0);
            transform: translateX(0);
    display: flex;
}

.closeModal {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
    display: none;
}
.courseNote {
    width: 50%;
    height: auto;
    margin: 0 auto;
    box-sizing: border-box;
    z-index: 100;
    padding: 2%;
    font-size: 1rem;
    text-align: center;
    background-color: white;
    border: 1px solid #888;
    border-radius: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.courseNote button {
    height: auto;
    background-color: white;
    margin-top: 2%;
    padding: 5px 12px;
    outline: 0;
    border: 1px solid #999;
    border-radius: 5px;
    font-size: 16px;
    font-family: "Noto Serif TC", serif;
    cursor: pointer;
    transition: 0.2s;
}

.courseNote button:hover,
.courseNote button:active {
    box-shadow: 2px 2px rgba(199, 199, 199, 0.5);
    background-color: rgb(248, 229, 203);
}

.pageTurner {
    margin: 1rem auto;
    display: flex;
    align-items: center;
    justify-content: center;
}

.pageTurner i {
    font-size: 30px;
    margin: 1%;
    border: 1px solid #888;
    border-radius: 50%;
    transition: 0.2s;
}

.pageTurner i:hover {
    background-color: rgb(248, 229, 203);
    cursor: pointer;
}

.hidePageButton {
    display: none;
}
.resultTable {
    box-sizing: border-box;
    width: 95%;
    margin: 2% auto;
    border-collapse: collapse;
    table-layout: auto;
}

.resultTable th,
.resultTable td {
    font-weight: 300;
    text-align: center;
    padding: 1%;
    border-bottom: 1px solid #333;
}

.admVeryHigh {
    color: purple;
}

.admHigh {
    color: #000088;
}

.admMid {
    color: green;
}

.admLow {
    color: darkgoldenrod;
}

.admNo {
    color: red;
}

.admImpossible {
    color: black;
}

.subjectName {
    width: 50%;
}

.additionalInfo {
    background-color: rgba(193, 66, 66, 0.1);
    cursor: pointer;
}

@media (max-width: 700px) {
    .uniSelect button {
        width: 40%;
    }

    .calMethod {
        display: none;
    }

    .subjectCode {
        font-size: 10px;
    }

    .subjectName {
        width: 50%;
    }
}

@media (max-width: 430px) {
    .resultTable th,
    .resultTable td {
        font-size: 14px;
    }
}
